@import "mixins";

:root {

--aqua:  #7FDBFF;
--blue:  #0074D9;
--navy:  #001F3F;
--teal:  #39CCCC;
--green: #2ECC40;
--olive: #3D9970;
--lime:  #01FF70;

--yellow:  #FFDC00;
--orange:  #FF851B;
--red:     #FF4136;
--fuchsia: #F012BE;
--purple:  #B10DC9;
--maroon:  #85144B;

--white:  #FFFFFF;
--silver: #DDDDDD;
--gray:   #AAAAAA;
--black:  #111111;

}

$base-colors: () !default;
$base-colors: map-merge((
    "aqua":    #7FDBFF,
    "blue":    #0074D9,
    "navy":    #001F3F,
    "teal":    #39CCCC,
    "green":   #2ECC40,
    "olive":   #3D9970,
    "lime":    #01FF70,
    "yellow":  #FFDC00,
    "orange":  #FF851B,
    "red":     #FF4136,
    "fuchsia": #F012BE,
    "purple":  #B10DC9,
    "maroon":  #85144B,
    "white":   #FFFFFF,
    "silver":  #DDDDDD,
    "gray":    #AAAAAA,
    "black":   #111111

), $base-colors);


$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px
) !default;
