@mixin bg-variant($parent, $color) {
  #{$parent} {
    background-color: $color !important;
  }
  #{$parent}-dk {
    background-color: darken($color, 10%) !important;
  }

  #{$parent}-dkr {
    background-color: darken($color, 20%) !important;
  }

  #{$parent}-lt {
    background-color: lighten($color, 10%) !important;
  }

  #{$parent}-ltr {
    background-color: lighten($color, 20%) !important;
  }

  a#{$parent} {
    @include hover-focus {
      background-color: darken($color, 10%) !important;
    }
  }
}

@mixin text-emphasis-variant($parent, $color) {
  #{$parent} {
    color: $color !important;
  }

  #{$parent}-dk {
    color: darken($color, 10%) !important;
  }

  #{$parent}-dkr {
    color: darken($color, 20%) !important;
  }

  #{$parent}-lt {
    color: lighten($color, 10%) !important;
  }

  #{$parent}-ltr {
    color: lighten($color, 20%) !important;
  }
  a#{$parent} {
    @include hover-focus {
      color: darken($color, 10%) !important;
    }
  }
}

@mixin hover-focus {
    &:focus,
    &:hover {
      @content;
    }
}

@mixin _assert-ascending($map, $map-name) {
  $prev-key: null;
  $prev-num: null;
  @each $key, $num in $map {
    @if $prev-num == null {
      // Do nothing
    } @else if not comparable($prev-num, $num) {
      @warn "Potentially invalid value for #{$map-name}: This map must be in ascending order, but key '#{$key}' has value #{$num} whose unit makes it incomparable to #{$prev-num}, the value of the previous key '#{$prev-key}' !";
    } @else if $prev-num >= $num {
      @warn "Invalid value for #{$map-name}: This map must be in ascending order, but key '#{$key}' has value #{$num} which isn't greater than #{$prev-num}, the value of the previous key '#{$prev-key}' !";
    }
    $prev-key: $key;
    $prev-num: $num;
  }
}

// Starts at zero
// Another grid mixin that ensures the min-width of the lowest breakpoint starts at 0.
@mixin _assert-starts-at-zero($map) {
  $values: map-values($map);
  $first-value: nth($values, 1);
  @if $first-value != 0 {
    @warn "First breakpoint in `$grid-breakpoints` must start at 0, but starts at #{$first-value}.";
  }
}
